@import url(https://fonts.googleapis.com/css2?family=Zen+Antique+Soft&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Zen+Antique+Soft&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: linear-gradient(to right, rgb(19, 38, 77),rgb(44, 33, 17));
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
.wrapper{
    height: 100vh;
    max-height: 67rem;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}



.button-wrapper{
    font-size: 2rem;
    padding: 1em 1em;
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
    border: 3px solid rgb(75, 7, 153);
    border-radius: 5%;
    font-family: 'Zen Antique Soft', serif;
}
.button-wrapper:hover{
    border: 3px solid currentColor;
    text-shadow: currentColor 1px 0 4px;
}


.calc-wrapper{
    height: 45rem;
    width: 29rem;
    display: flex;
    flex-direction: column;
    align-items: center;
}
.buttons{
    height: 35rem;
    width: 100%;
    display: grid;
    grid-template-columns: repeat(4,7rem);
    grid-template-rows: repeat(6,5.7rem);
    grid-gap: 0.35rem;
    gap: 0.35rem;
    padding: 0.25rem 0;
}
.calculator{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 90rem;
    grid-gap: 0.5rem;
    gap: 0.5rem;
}
.themeBtn{
    font-size: 1.25rem;
    padding: 1em 2em;
    background: #000;
    color: #fff;
    border: none;
    box-shadow: 0 0 4px 0 rgb(230, 4, 230);
    cursor: pointer;
    transition: all 500ms ease-in;
}
.themeBtn:hover,.themeBtn:focus-visible{
    box-shadow: 0 0 10px 0 rgb(230, 4, 230);
    outline: none;
}
@media(max-width:500px){
    html{
        font-size: 62.5%;
    }
}
@media(max-height:800px){
    html{
        font-size: 62.5%;
    }
}
@media(max-height:520px){
    html{
        font-size: 43.5%;
    }
}
@media(max-width:300px){
    html{
        font-size: 43.5%;
    }
}
.output-wrapper{
    display: flex;
    justify-content: space-around;
    align-items: flex-end;
    flex-direction: column;
    justify-content: space-evenly;
    width: 100%;
    height: 9rem;
    font-family: 'Zen Antique Soft', serif;
    text-align: end;
}
.result{
    font-size: 2rem;
    text-shadow: currentColor 0px 0 2px;
}
.userInput{
    font-size: 1.8rem;
    overflow-wrap: break-word;
    overflow-y: scroll;
    width: 100%;
    max-width: 100%;
    scroll-behavior: smooth;
}
.userInput::-webkit-scrollbar{
    width: 2px;
}
.userInput::-webkit-scrollbar-thumb{
    background-color: rgb(255, 255, 255);
}
.userInput::-webkit-scrollbar-track{
    background-color: #000;
}


